.AllMediaSection{
    display: flex;
    flex-flow: row wrap;
    width: 90%;
    margin: auto;
    justify-content: center;
    align-items: center;
}

.mediaSection{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin: 5px auto;
    box-shadow: 0 2px 3px #ddd;
    cursor: pointer;
}
.mediaSection img{
    width: 200px;
    height: 200px;
}

.ModalAllMediaSection{
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;
}

.ModalMediaSection{
    width: 25%;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px 5px;
    box-shadow: 0 2px 3px #ccc;
    cursor: pointer;
    padding: 5px;
    position: relative;
}
.ModalMediaSection img{
    width: 100%;
    height: 100%;
}
.checkBox{
    position: absolute;
    top: 2px;
    left: 0;
}
.BoxImage{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}